//Meny

.menu{
    margin-left: auto;
    margin-right: auto;
    display: block;
    text-align: center;
    margin-right: 2em;
    margin-top: -0.5em;
    margin-bottom: 2em;

ul li{
list-style-type: none;
display: inline;
margin-left: 0.5em;
margin-right: 0.5em;

a{
    text-decoration: none;
    color: #202020;
    font-family: serif;
    font-size: 1em;
    letter-spacing: 4px;
    text-transform: uppercase;
//inkluderar styling för mobil
    @include mobile {
        font-size: 0.8em;
     }

     //inkluderar inställningar för dark mode
     @include darkMode {
      color: $lightColor;
  }
//inkluderar hover och active endast för desktop
&:hover{
  @include desktop {
    color: $darkpink;  
  }
&:active{
  @include desktop {
    color: $pink;
  }
}
}
}
}
}
#current{
    a{  color: $darkpink;}
}

#admin{
   text-align: center;
   margin-left: -2em;
    margin-top: -1.5em;
    a{
        text-decoration: none;
        color: #202020;
        font-family: serif;
        font-size: 1em;
        letter-spacing: 4px;
        text-transform: uppercase;
         //inkluderar inställningar för dark mode
     @include darkMode {
        color: $lightColor;
    }
  //inkluderar hover och active endast för desktop
  &:hover{
